export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={date => setStartDate(date)}\n      popperClassName=\"some-custom-class\"\n      popperPlacement=\"top-end\"\n      popperModifiers={{\n        offset: {\n          enabled: true,\n          offset: \"5px, 10px\"\n        },\n        preventOverflow: {\n          enabled: true,\n          escapeWithReference: false,\n          boundariesElement: \"viewport\"\n        }\n      }}\n    />\n  );\n};\n";