export default "() => {\n  const [startDate, setStartDate] = useState(null);\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={date => setStartDate(date)}\n      minDate={new Date()}\n      maxDate={addMonths(new Date(), 5)}\n      showDisabledMonthNavigation\n    />\n  );\n};\n";