export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  const ExampleCustomInput = ({ value, onClick }) => (\n    <button className=\"example-custom-input\" onClick={onClick}>\n      {value}\n    </button>\n  );\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={date => setStartDate(date)}\n      customInput={<ExampleCustomInput />}\n    />\n  );\n};\n";